@import url('https://fonts.googleapis.com/css?family=Montserrat|Staatliches&display=swap');

:root {
	background-color: black;
}

.app {
	height: 100vh;
	display: flex;
	color: white;
	font-family: 'Montserrat', sans-serif;
}
  
.app-left {
	width: 50%;
	background-color: rgb(208, 130, 94);
	overflow: auto;
}
  
.app-right {
	width: 50%;
	background-color: #2E4158;
	padding: 25px 40px;
	overflow: auto;
}
  
.toggle {
	display: flex;
	justify-content: flex-end;
	margin-bottom: 10%;
}
  
.toggle-item {
	background-color: #4C5D72;
	color: #9DA6B1;
	padding: 10px 25px;
	cursor: pointer;
	font-size: .9em;
	border: none;
	outline: none;
	display: inline-block;
	text-decoration: none;
}
  
.toggle-item-active {
	background-color: rgb(208, 130, 94);
	color: white;
}
  
.toggle-item:first-child {
	border-top-left-radius: 25px;
	border-bottom-left-radius: 25px;
}

.toggle-item:last-child {
	border-top-right-radius: 25px;
	border-bottom-right-radius: 25px;
}
  
.form-main-container {
	margin-bottom: 100px;
}
  
.form-item {
	margin-bottom: 40px;
}
  
.form-item-label {
	display: block;
	text-transform: uppercase;
	font-size: .9em;
	color: white;
}
.form-item-input {
	width: 85%;
	background-color: transparent;
	border: none;
	color: white;
	outline: none;
	border-bottom: 1px solid #445366;
	font-size: 1em;
	font-weight: 300;
	padding-bottom: 10px;
	margin-top: 10px;
}
  
.form-item-input::placeholder {
	color: #616E7F;
}
  
.form-item-button {
	background-color: rgb(208, 130, 94);
	color: white;
	border: none;
	outline: none;
	border-radius: 25px;
	padding: 15px 70px;
	font-size: .8em;
	font-weight: 500;
}
 
.form-item-checkbox-label {
	color: #646F7D;
	font-size: .9em;
}
  
.form-item-checkbox {
	position: relative;
	top: 1.5px;
}
  
.form-item-tos {
	color: white;
	border-bottom: 1px solid rgb(208, 130, 94);
	text-decoration: none;
	display: inline-block;
	padding-bottom: 2px;
	margin-left: 5px;
}